import React from 'react'

const Chat = ({chat}) => {
  return (
    <div className='flex border-down bg-gray-100 m-1'>
        <img className='w-10 rounded-full' alt='profile' src={`https://source.unsplash.com/random/200x200?sig=${Math.floor(Math.random()*20)}`}></img>
        <span className='m-2'>{chat.name} :</span>
        <span className='flex items-center'>{chat.message.length==0?"hi":chat.message}</span>
    </div>
  )
}

export default Chat