import React from 'react'
import { useSelector } from 'react-redux'

const Siderbar = () => {
  const visibile = useSelector((state)=>state.Sidebar.isVisible);
 
  if(!visibile)  return null ;

  return (
    <div className='w-72 '>
        <ul className='h-52 pl-8 py-4 px-10' >
            <li>Home</li>
            <li>Shorts</li>
            <li>Subscriptions</li>
            <li>YoutubeMusic</li>
        </ul>
    </div>
  )
}

export default Siderbar